@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300;400&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently supported by Chrome, Edge, Opera and Firefox */
}

body {
  background-color: #fafafa;
}

footer {
  position: fixed;
  bottom: 0;
  width: 100%;
}

.main_content {
  padding: 20px;
  padding-top: 65px;
  padding-bottom: 80px;
}


/* Backgrounds */
.bg_gray {
  background-color: #6c757d;
}

.bg_green {
  background-color: seagreen;
}

.bg_blue {
  background-color: #2196f3;
}

.bg_yellow {
  background-color: #ffc107;
}

.bg_red {
  background-color: #f44336;
}

.bg_red_str {
  background-color: #b71c1c;
}

.bg_purple {
  background-color: #9c27b0;
}

.bg_purple_str {
  background-color: #4a148c;
}

.bg_black {
  background-color: #212121;
}

/* BUTTONS */
.btn_bc_green {
  background-color: #28a745;
}

.btn_bc_green:active {
  background-color: #208638;
}

.btn_bc_blue {
  background-color: #007bff;
}

.btn_bc_blue:active {
  background-color: #015fc4;
}

.btn_bc_red {
  background-color: #dc3545;
}

.btn_bc_red:active {
  background-color: #be2d3c;
}

.btn_bc_yellow {
  background-color: #ffc107;
}

.btn_bc_yellow:active {
  background-color: #dba501;
}
@media (max-width: 720px) {
  .app-modal {
    width: 80% !important;
  }
}

.app-modal {
  margin: auto;
  padding: 25px 20px 0 20px;
  text-align: center;
  border-radius: 10px 10px 0 0 !important;
  height: auto !important;
}

.hyperCloseIconWrapper___ujoQX {
  display: none !important;
}

.app-modal-p0 {
  padding: 0 !important;
}

.menu-modal-container {
  background-color: transparent !important;
  width: auto !important;
  height: auto !important;
  margin-top: 54px !important;
  border-radius: 0 0 10px 10px !important;
}
.info-modal .word-container h3 {
  font-family: 'Noto Sans JP', sans-serif;
  font-weight: 400;
  font-size: 24px;
  color: #39414D;
}

.info-modal .word-container h4 {
  font-weight: 300;
  margin-top: 5px;
  font-size: 20px;
  color: #5E6670;
}

.info-modal .tag-container {
  margin-top: 25px;
}

.info-modal .tag-container .modal-tag-item {
  margin: 0 5px;
}

.info-modal .actions {
  margin-top: 25px;
  display: flex;
  justify-content: space-around;
  margin-left: -20px;
  width: calc(100% + 40px);
  border-radius: 0 0 8px 8px;
}
.add-modal .actions {
  margin-top: 25px;
  display: flex;
  justify-content: space-around;
  margin-left: -20px;
  width: calc(100% + 40px);
  border-radius: 0 0 8px 8px;
}

.add-modal .word-container input {
  border: 1px solid #C7D0D8;
  border-radius: 3px;
  padding: 10px;
  width: -webkit-fill-available;
  margin-top: 25px;
  font-size: 16px;
}

.add-modal .word-container input:first-child {
  margin-top: 0;
}

.add-modal .word-container .japanese {
  font-family: 'Noto Sans JP', sans-serif;
  font-size: 14px;
}

.add-modal .match-area {
  margin-top: 15px;
  display: block;
}

.add-modal .match-area span {
  font-family: 'Noto Sans JP', sans-serif;
}

.add-modal .match-area span em {
  font-family: 'Roboto', sans-serif;
}
.menu-modal {
  background-color: #fefefe;
  margin: auto;
  margin-right: 0;
  border: 1px solid #888;
  width: 200px;
  text-align: center;
  border-radius: 3px;
}

.menu-modal .menuItem {
  padding: 20px 15px;
  border-top: 1px solid #cfd8dc;
  transition: background-color .1s;
}

.menu-modal .menuItem:active{
  background-color: rgba(0, 0, 0, 0.1);
}

.menu-modal .menuItem:first-child{
  border: none;
}

.config-modal .actions {
  margin-top: 25px;
  display: flex;
  justify-content: space-around;
  margin-left: -20px;
  width: calc(100% + 40px);
  border-radius: 0 0 8px 8px;
}

.config-modal h2 {
  font-weight: 200;
  font-size: 20px;
}

.config-modal input {
  width: 50%;
  border: 1px solid #C7D0D8;
  border-radius: 3px;
  padding: 10px;
  margin-top: 25px;
  font-size: 24px;
  text-align: center;
}
header {
  background-color: #2196f3;
  color: #ffffff;
  padding: 15px;
  position: fixed;
  width: -webkit-fill-available;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

header span {
  font-family: 'Noto Sans JP', sans-serif;
  font-weight: 300;
  display: flex;
  align-items: center;
}

header img {
  width: 30px;
  height: 21px;
  margin-right: 5px;
}

header i {
  transition: background-color .1s;
}

header i:active {
  color: rgba(255, 255, 255, .5);
}
.playing-bar {
  background-color: #673ab7;
  z-index: 100;
  display: none;
  padding: 10px;
  color: #ffffff;
  box-shadow: 0px 0px 13px 0px rgba(0,0,0,0.75);
  animation: fadeInUp;
  animation-duration: .2s;
}

.playing-bar-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.playing-bar span {
  letter-spacing: 1px;
  margin-left: 10px;
  font-size: 14px;
}

.playing-bar i {
  font-size: 45px;
  transition: color .1s;
}

.playing-bar i:active {
  font-size: 45px;
  color: rgba(255, 255, 255, .5);
}

.playing-bar .right-container {
  display: flex;
  align-items: center;
}
.playing-tooltip {
  display: flex;
  padding: 3px 4px;
  border-radius: 5px;
  align-items: center;
  font-size: 10px;
  color: #5E6670;
}

.playing-tooltip i {
  font-size: 12px;
  color: red;
  transition: color .1s;
  margin-top: -1px;
}
.card-container {
  border: 1px solid #eaeaea;
  background-color: #ffffff;
  border-radius: 5px;
  display: flex;
  transition: background-color .1s;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 1px 0px;
}

.card-container:first-child {
  margin-top: 0;
}

.card-container:active {
  background-color: rgba(0, 0, 0, .1);
}

.card-container .word-item-container {
  width: 100%;
}

.card-container .word-item-container .word-container {
  padding: 10px 15px;
}

.card-container .word-item-container .word-container h3 {
  font-family: 'Noto Sans JP', sans-serif;
  font-weight: 400;
  color: #39414D;
}

.card-container .word-item-container .word-container h5 {
  font-family: 'Noto Sans JP', sans-serif;
  font-weight: 200;
  color: #39414D;
}

.card-container .word-item-container .word-container .inline-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card-container .word-item-container .word-container h4 {
  font-weight: 300;
  color: #5E6670;
}

.card-container .word-item-container .word-container span {
  font-weight: 100;
  color: #ffffff;
  font-size: 10px;
  padding: 1px 5px;
  border-radius: 5px;
  min-width: 22px;
  text-align: center;
}

.card-container .word-item-container .word-container .mt2 {
  margin-top: 2px;
}
.menuItem {
  display: flex;
  align-items: center;
  font-size: 17px;
}

.menuItem span {
  color: #000000;
}

.menuItem i {
  margin-right: 5px;
  font-size: 22px;
  color: #5E6670;
}

.smallest {
  font-size: 20px !important;
  margin-left: 3px;
}
.modal-tag-item {
  display: inline-flex;
  padding: 4px 5px;
  border-radius: 5px;
  align-items: center;
}

.modal-tag-item span {
  color: #ffffff;
  font-size: 12px;
}

.modal-tag-item i {
  font-size: 14px;
  color: #ffffff;
  transition: color .1s;
  margin-right: 3px;
}
.modal-btn {
  padding: 13px 0;
  width: -webkit-fill-available;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color .1s;
  font-size: 12px;
}

.modal-btn i {
  margin-right: 5px;
  font-size: 14px;
}
.language-item {
  display: flex;
  align-items: center;
  border: 1px solid #b0bec5;
  padding: 15px 25px;
  border-radius: 5px;
  margin-bottom: 2rem;
  background-color: #fff;
}

.language-item:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

.language-item img {
  width: 80px;
  height: 60px;
  margin-right: 20px;
}

.language-item h1 {
  font-weight: 200;
}
.welcomeScreen {
  display: flex;
  width: 100%;
  height: 100vh;
  align-items: center;
  justify-content: space-evenly;
  background-color: #f5f5f5;
}

.welcomeScreen .container {
  text-align: center;
}

.welcomeScreen .container h2 {
  margin-bottom: 3rem;
  font-weight: 200;
  font-size: 2rem;
}
